import {
  MIN_SONGS_PLAYLIST,
} from './SpotifyConstants';

export function generateQuestions(tracks) {
  // jag vet inte riktigt rtt svar nr jag genererar d tracks som kommer in bara r en players tracks
  // s vilka som har en track i sin lista fr jag kolla senare live.

  // q types:
  // VILKEN R DEN SENAASTE LTEN SOM playerxx lagt till? vilken var den frst?
  // vem har flest ltar av artist X?
  // vem har denna lt som mest spelad?
  // vilket lt har person x som mest spelad?

  const questions = tracks.map((t) => {
    const q = {
      type: 'guess_owner',
      track: t,
    };
    return q;
  });
  return questions;
}

export function isValidPlaylist(playlist) {
  if (playlist.tracks.total < MIN_SONGS_PLAYLIST) {
    return false;
  }

  return true;
}

export default[generateQuestions, isValidPlaylist];
