import React, { Component } from 'react';
import PropTypes from 'prop-types';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { Typography } from '@material-ui/core';
import SpotifyResultQuestion from './SpotifyResultQuestion';
import { fire } from '../../../../base';
import * as util from '../SpotifyUtil';
import {
  AUTH_EXPIRE_MS,
  MISSING_ALBUM_COVER,
  CLIENT_ID,
  SONG_VOLUME_FADE_TIME,
} from '../SpotifyConstants';
import PhaseAnswer from '../../../quiz/host/PhaseAnswer';

class Spotify extends Component {
  constructor(props) {
    super(props);

    this.state = {
      spotifytoken: localStorage.getItem('spotifytoken') || '',
      tokentimestamp: localStorage.getItem('spotifytoken_timestamp') || '',
      tracks: [],
      questions: [],
      usedQuestions: [],
      songplaying: false,
      currentQuestion: 0,
    };

    this.nextPhase = this.nextPhase.bind(this);
    this.playQuestion = this.playQuestion.bind(this);
    this.saveGame = this.saveGame.bind(this);
    this.nextQuestion = this.nextQuestion.bind(this);
  }


  componentDidMount() {
    const { game } = this.props;
    const { spotifytoken, tokentimestamp } = this.state;

    if (!spotifytoken || Date.now() - tokentimestamp > AUTH_EXPIRE_MS) {
      const authEndpoint = 'https://accounts.spotify.com/authorize';
      // Replace with your app's client ID, redirect URI and desired scopes
      const redirectUri = window.location.origin;
      const scopes = [
        'user-top-read',
      ];
      window.location = `${authEndpoint}?client_id=${CLIENT_ID}&redirect_uri=${redirectUri}&scope=${scopes.join('%20')}&response_type=token&show_dialog=true`;
    }
    const playlists = Object.values(game.players).map(player => player.playlist);

    const myHeaders = new Headers();
    myHeaders.append('Authorization', `Bearer ${spotifytoken}`);
    let playerArray = Object.values(game.players);
    playerArray = playerArray.concat(playerArray).concat(playerArray);
    const maxQuestionsPerList = Math.ceil((game.minigame.questions * 2) / playerArray.length);
    playerArray.forEach((player, index) => {
      fetch(`https://api.spotify.com/v1/playlists/${player.playlist}/tracks`, {
        method: 'GET',
        headers: myHeaders,
      }).then(response => response.json())
        .then((data) => {
          console.table(data);
          const tracks = data.items.map((t) => {
            const track = {
              added_at: t.added_at.split('T')[0],
              name: t.track.name,
              audio: t.track.preview_url,
              artists: t.track.artists.map(a => a.name).join(', '),
              img: t.track.album.images.length > 0 ? t.track.album.images[0] : MISSING_ALBUM_COVER,
              playerKey: player.key,
              playerName: player.name,
              id: t.track.id,
            };
            return track;
          }).filter(t => t.audio);
          // ta bort fr mnga. sortera alla p popularitet och sen stt length till 10(?) om det r ver 10
          // ta bort dem frrn question inte frn track fr d riskerar rtt svar att bli felaktiga
          // d r alla playlists tminstone samma storlek. kan ven lgga till validering p playlistSelector att det mste finnas minst 10ltar med preview_url
          this.setState((state) => {
            // sortera efter populritet eller ngot annat? senast tillagda?
            const questionTracks = tracks.sort(() => Math.random() - 0.5);
            if (questionTracks.length > maxQuestionsPerList) {
              questionTracks.length = maxQuestionsPerList;
            }
            const questions = util.generateQuestions(questionTracks);
            return ({
              questions: [...state.questions, ...questions].sort(() => Math.random() - 0.5),
              tracks: [...state.tracks, ...tracks],
            });
          }, () => {
            if (index + 1 === playerArray.length) {
              // det r sista loopen
              this.playQuestion();
            }
          });
        });
    });


    // hr ska jag plocka ut alla spelares playlists
    // skapa upp frstelig struktur av det, plocka ut det vesentliga s som artist, lt, prewvie_url, cover_art, ownerName, id
    // generera frgor och svar. brja med frgan: vem har denna lt i sin spellista? frgan innehller: frgetext, rtta svar [], songId,
    // rtt svar vet jag inte riktigt nr jag genererar
    // rtta svar stts genom svar= songs.filter(x=> x.id===songId).map(song=> song.ownerName);
    // allts en array med alla ownernames som har denna sng.
    // vad behver sparas till firebase? inget? i play.js fr man se vad man svarade. i host visas allas svar likt quiz.
    // s lnge svarsalterntiven alltid r detsamma (spelarna) s behver jagi nte updatera firebase med information om frgorna.
    //
    // score likt quiz beroende p hur snabb man var. jag kan visa albumart direkt och sen namn och artitst kommer senare fr att frsvrar?
    // livlinor?
    // spara frgor och svar till state
  }

  shouldComponentUpdate() {
    // kan jag ha det hr?

    return true;
  }


  handleChange = name => (event) => {
    this.setState({
      [name]: event.target.value,
    });
  };


  handleChangeSelect = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };

  nextQuestion() {
    this.setState(state => ({
      currentQuestion: state.currentQuestion + 1,
    }), this.playQuestion);
  }

  playQuestion() {
    const { questions, currentQuestion, tracks } = this.state;
    // testa detta med fuskgrejen igen att skapa 4 players till foreachen
    // testa kra en await eller then p setstate? hr eller tidigare i loopen. jag vill vara sker p att ha ftt alla frgor hr
    // blanda frgorna random?
    // https://medium.learnreact.com/setstate-takes-a-callback-1f71ad5d2296


    // /kr ngon metod som r "nextquestion"
    // initquiz krs bara en gng, nextquestion krs infr varje frga.

    this.setState({ songplaying: true });
    console.table(questions);
    const audio = new Audio(questions[currentQuestion].track.audio);
    audio.volume = 0.1;
    audio.play();
    audio.onended = () => {
      this.nextPhase();
    };
    // fade in and out
    audio.ontimeupdate = () => {
      const left = audio.duration - audio.currentTime;
      const fadeout = left <= SONG_VOLUME_FADE_TIME;
      if (fadeout) {
        audio.volume = left / SONG_VOLUME_FADE_TIME;
      } else if (audio.volume < 1) {
        audio.volume = Math.min(1, audio.volume + 0.2);
      }
    };
    // generera questions hr istllet?
    // kolla PhaseAnswer.js
    // behver en timer likt det fast fr ligga undern gon metod som denna?
    // hrt p 30sek, inte bry sig om om folk svarat innan
    // nextphase blir till level_complete
    // nstafrga fr jag klicka p inne i resultquestion. d ska phase sttas tillabka till gameplay och currentquiestion ska kas p 1.
    // synka currenteustion p game.minigame i firebase. om det inte fungerar fr jag pdatera den genom ngon extra metod som pekat till parent frn resultquestion component.

    // kolla p quiz hur jag lser fldet, hur vet jag nr en frga r besvarad?
    // mste kolla mot gameObj
    // jag behver ocks ta in en timer likt quizet! det r den som styr nr det r klart, nr nsta frga ska kras osv.
    // ngon metod krs konstant under timern, typ render fr att updatera timern och rendrera ledtrdar under spelets gng?
    // nr timern r klar krs en "eventhandler" som kr nextphase
    // i SpotifyResultQuestion s stts nextphase tillbaka till gameplay fr att f nsta frga. currentquestion rknas upp vid det klicket? behver en action in till komponenten isf
    // annars kan vi rkna upp den hr inne p ngot stt skert.
  }

  saveGame(game) {
    const { gameFunc } = this.props;
    gameFunc.update(game);
  }

  nextPhase() {
    const { game } = this.props;
    const { currentQuestion } = this.state;
    this.setState({ songplaying: false });
    if (currentQuestion >= game.minigame.questions) {
      game.phase = 'final_result';
    } else {
      game.phase = 'level_completed';
    }
    this.saveGame(game);
  }

  render() {
    const { game, gameFunc } = this.props;
    const {
      tracks, questions, currentQuestion, songplaying,
    } = this.state;
    if (game.phase === 'gameplay' && !songplaying) {
      return (
        <div>loading questions</div>
      );
    }
    const question = questions[currentQuestion];

    if (game.phase === 'level_completed') {
      // game behver vara uppdaterat med svar frn spelarna och den senaste frgan?
      // i firebase kommer man bara se player.answers och inte quiz.questions. men vi behver synka currentquestionindex kanske?
      // det r i resultqestion som jag rttar svaren s jag behver ju hela tracks dr och currentquestion
      return (
        <SpotifyResultQuestion game={game} gameFunc={gameFunc} tracks={tracks} question={question} nextQuestion={this.nextQuestion} />
      );
    }


    return (
      <div>

        {question.type === 'guess_owner' && (
          <div>
            <Typography>Vems lt r detta?</Typography>
            <img src={question.track.img.url} alt="cover art" />

            <Typography>{question.track.name}</Typography>
            <Typography>{question.track.artists}</Typography>

          </div>
        )}
      </div>
    );
  }
}
Spotify.propTypes = {
  gameFunc: PropTypes.object.isRequired,
  game: PropTypes.object.isRequired,
};
export default Spotify;
